.Container{
  width: 400px;
  margin: 10px auto 0;
  
  padding: 10px;
}

.Skill li {
  position: relative;
  list-style: none;
  margin: 15px 0;
  font-weight: 500;
  text-transform: uppercase;
}

.Skill li::before{
  content: "";
  position: absolute;
  top: calc(100% + 2.5px);
  left: 0;
  width: 100%;
  height: 15px;
  background-color: rgba(223, 107, 107, 0.5);
  border-radius: 1000px;
}

.Skill li::after{
content: "";
position: absolute;
top: calc(100% + 2.5px);
left: 0;
width: 0;
height: 15px;
background-color: rgb(245, 9, 9);
border-radius: 1000px;
-webkit-animation-duration: .7s;
        animation-duration: .7s;
-webkit-animation-timing-function: ease;
        animation-timing-function: ease;
-webkit-animation-fill-mode: forwards;
        animation-fill-mode: forwards;
}

.Skill li.html::after{
  -webkit-animation-name: html;
          animation-name: html;
}

@-webkit-keyframes html{
to {
  width: 70%;
}
}

@keyframes html{
to {
  width: 70%;
}
}

.Skill li.css::after{
-webkit-animation-name: css;
        animation-name: css;
}

@-webkit-keyframes css{
to {
width: 60%;
}
}

@keyframes css{
to {
width: 60%;
}
}

.Skill li.php::after{
-webkit-animation-name: php;
        animation-name: php;
}

@-webkit-keyframes php{
to {
width: 50%;
}
}

@keyframes php{
to {
width: 50%;
}
}

.Skill li.javascript::after{
-webkit-animation-name: javascript;
        animation-name: javascript;
}

@-webkit-keyframes javascript{
to {
width: 60%;
}
}

@keyframes javascript{
to {
width: 60%;
}
}

.Skill li.react::after{
-webkit-animation-name: react;
        animation-name: react;
}

@-webkit-keyframes react{
to {
width: 60%;
}
}

@keyframes react{
to {
width: 60%;
}
}

.Skill li.python::after{
-webkit-animation-name: python;
        animation-name: python;
}

@-webkit-keyframes python{
to {
width: 40%;
}
}

@keyframes python{
to {
width: 40%;
}
}

.Skill li.java::after{
-webkit-animation-name: java;
        animation-name: java;
}

