.hero {
  position: relative;
}
.hero h3 {
  font-weight: 100;
  letter-spacing: 2px;
  margin-left: 10%;
}
.hero h1 {
  font-size: 55px;
  font-weight: bold;
  margin-left: 10%;
}

.hero h1 span {
  color: #ff00ea;
  
}
.hero h2 {
  font-size: 45px;
  font-weight: bold;
  color: #000000;
  margin-left: 10%;
}
.hero h2 span {
  color: rgb(253, 0, 0);
  font-size: 40px;
}
.hero p {
  color: #878e99;
  font-size: 18px;
  line-height: 30px;
  margin-top: 30px;
  margin-left: 10%;
  margin-right: 10%;
}
.hero_btn {
  margin-top: 20%;
}
.hero_btn h4 {
  font-weight: 500;
  letter-spacing: 2px;
  color: #43474b;
  font-size: 15px;
  margin: 30px 0 30px 0;
}
.hero_btn button {
  margin-right: 30px;
}
.hero_btn i {
  font-size: 20px;
}

.hero_btn button img {
  width: 20px;
  height: 20px;
}
.hero .left {
  margin-right: 10%;
}
.hero .right {
  width: 40%;
  position: relative;
}


.hero .right_img img {
  position: absolute;
  top: -18%;
  right: 30%;
  width: 320px;
  height: 400px;
}
@media (max-width: 768px) {
  .hero .right,
  .hero .left {
    width: 100%;
    margin-left: 20px;
  }

  .hero .f_flex {
    flex-direction: column;
    flex-direction: column-reverse;
  }
  .hero .right_img {
    width: 100%;
    height: 54vh;
  }
}
