

.Skill .details p {
  font-size: 17px;
  margin-left: 10%;
  margin-right: 10%;
}

.Skill .details h2 {
  font-size: 32px;
  margin-left: 10%;
  margin-right: 10%;
}

.image-container {
  display: flex;
  justify-content: right;
  align-items: right;
  padding: auto;
  
 
}

.Skill .left {
  width: 35%;
}
.Skill .right {
  width: 60%;
}

.Container{
  width: 400px;
  margin: 10px auto 0;
  
  padding: 10px;
}

.Skill li {
  position: relative;
  list-style: none;
  margin: 15px 0;
  font-weight: 500;
  text-transform: uppercase;
}

.Skill li::before{
  content: "";
  position: absolute;
  top: calc(100% + 2.5px);
  left: 0;
  width: 100%;
  height: 15px;
  background-color: rgba(223, 107, 107, 0.5);
  border-radius: 1000px;
}

.Skill li::after{
content: "";
position: absolute;
top: calc(100% + 2.5px);
left: 0;
width: 0;
height: 15px;
background-color: rgb(245, 9, 9);
border-radius: 1000px;
animation-duration: .7s;
animation-timing-function: ease;
animation-fill-mode: forwards;
}

.Skill li.html::after{
  animation-name: html;
}

@keyframes html{
to {
  width: 70%;
}
}

.Skill li.css::after{
animation-name: css;
}

@keyframes css{
to {
width: 60%;
}
}

.Skill li.php::after{
animation-name: php;
}

@keyframes php{
to {
width: 50%;
}
}

.Skill li.javascript::after{
animation-name: javascript;
}

@keyframes javascript{
to {
width: 60%;
}
}

.Skill li.react::after{
animation-name: react;
}

@keyframes react{
to {
width: 60%;
}
}

.Skill li.python::after{
animation-name: python;
}

@keyframes python{
to {
width: 40%;
}
}

.Skill li.java::after{
animation-name: java;
}

@keyframes java{
to {
width: 40%;
}
}

.Skill li.mysql::after{
animation-name: mysql;
}

@keyframes mysql{
to {
width: 70%;
}
}